import React from 'react';
import styled from 'styled-components';
import { motion } from 'framer-motion';

const MissionContainer = styled.div`
  padding-top: 100px;
`;

const HeroSection = styled.section`
  padding: 3rem 0;
  background-color: ${props => props.theme.colors.primary};
  color: ${props => props.theme.colors.lightCream};
  text-align: center;
`;

const HeroTitle = styled.h1`
  font-size: 3rem;
  margin-bottom: 1.5rem;
  
  @media (max-width: ${props => props.theme.breakpoints.tablet}) {
    font-size: 2.5rem;
  }
`;

const ContentSection = styled.section`
  padding: 5rem 0;
  background-color: ${props => props.theme.colors.lightCream};
`;

const ContentContainer = styled.div`
  max-width: 800px;
  margin: 0 auto;
  padding: 0 1rem;
`;

const ContentHeading = styled(motion.h2)`
  color: ${props => props.theme.colors.primary};
  font-size: 2.5rem;
  margin-bottom: 2rem;
`;

const ContentText = styled(motion.p)`
  color: ${props => props.theme.colors.text};
  font-size: 1.1rem;
  line-height: 1.8;
  margin-bottom: 2rem;
`;

const ValuesSection = styled.section`
  padding: 5rem 0;
  background-color: white;
`;

const ValuesHeading = styled(motion.h2)`
  text-align: center;
  color: ${props => props.theme.colors.primary};
  font-size: 2.5rem;
  margin-bottom: 3rem;
`;

const ValuesGrid = styled.div`
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1rem;
`;

const ValueCard = styled(motion.div)`
  background-color: ${props => props.theme.colors.lightCream};
  padding: 2rem;
  border-radius: 8px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.05);
`;

const ValueTitle = styled.h3`
  color: ${props => props.theme.colors.primary};
  margin-bottom: 1rem;
  font-size: 1.5rem;
`;

const ValueDescription = styled.p`
  color: ${props => props.theme.colors.text};
  line-height: 1.7;
`;

const ApproachSection = styled.section`
  padding: 5rem 0;
  background-color: ${props => props.theme.colors.lightCream};
`;

const ApproachContainer = styled.div`
  max-width: 800px;
  margin: 0 auto;
  padding: 0 1rem;
`;

const ApproachHeading = styled(motion.h2)`
  color: ${props => props.theme.colors.primary};
  font-size: 2.5rem;
  margin-bottom: 2rem;
  text-align: center;
`;

const Mission = () => {
  const coreValues = [
    {
      title: "Gven",
      description: "Dananlarmzla gvene dayal ilikiler kurar, gizlilik ve profesyonellii en st dzeyde tutarz."
    },
    {
      title: "Empati",
      description: "Her dananmzn benzersiz deneyimlerini anlamak iin derin bir empati ile yaklarz."
    },
    {
      title: "Bilimsellik",
      description: "Uygulamalarmz gncel bilimsel aratrmalar ve kanta dayal tekniklerle ekillendiririz."
    },
    {
      title: "Kapsayclk",
      description: "Farkl gemilere, kltrlere ve ihtiyalara sahip tm bireyleri kucaklayan bir yaklam benimseriz."
    }
  ];

  return (
    <MissionContainer>
      <HeroSection>
        <div className="container">
          <HeroTitle>Misyonumuz</HeroTitle>
        </div>
      </HeroSection>
      
      <ContentSection>
        <ContentContainer>
          <ContentHeading
            initial={{ y: 50, opacity: 0 }}
            whileInView={{ y: 0, opacity: 1 }}
            transition={{ duration: 0.6 }}
            viewport={{ once: true }}
          >
            Ard Psikoloji'nin Misyonu
          </ContentHeading>
          
          <ContentText
            initial={{ y: 50, opacity: 0 }}
            whileInView={{ y: 0, opacity: 1 }}
            transition={{ duration: 0.6, delay: 0.2 }}
            viewport={{ once: true }}
          >
            Ard Psikoloji olarak misyonumuz, bilimsel temelli ve etik deerlere bal kalarak, 
            bireylerin ruhsal saln iyiletirmek ve yaam kalitelerini artrmaktr. Her dananmzn
            benzersiz ihtiyalarn anlayarak, onlara zg zmler sunmay ve kiisel geliimlerine 
            elik etmeyi amalyoruz.
          </ContentText>

          <ContentText
            initial={{ y: 50, opacity: 0 }}
            whileInView={{ y: 0, opacity: 1 }}
            transition={{ duration: 0.6, delay: 0.4 }}
            viewport={{ once: true }}
          >
            Toplumda ruh sal konusunda farkndalk yaratmak, damgalanmay azaltmak ve herkesin 
            ihtiya duyduunda kaliteli psikolojik destek alabilmesini salamak iin alyoruz. 
            Empati, sayg ve profesyonellik ilkelerimizle, dananlarmzn gvenilir rehberi olmay hedefliyoruz.
          </ContentText>
        </ContentContainer>
      </ContentSection>

      <ValuesSection>
        <ValuesHeading
          initial={{ y: 50, opacity: 0 }}
          whileInView={{ y: 0, opacity: 1 }}
          transition={{ duration: 0.6 }}
          viewport={{ once: true }}
        >
          Temel Deerlerimiz
        </ValuesHeading>

        <ValuesGrid>
          {coreValues.map((value, index) => (
            <ValueCard
              key={index}
              initial={{ y: 50, opacity: 0 }}
              whileInView={{ y: 0, opacity: 1 }}
              transition={{ duration: 0.6, delay: index * 0.2 }}
              viewport={{ once: true }}
              whileHover={{ y: -10, transition: { duration: 0.3 } }}
            >
              <ValueTitle>{value.title}</ValueTitle>
              <ValueDescription>{value.description}</ValueDescription>
            </ValueCard>
          ))}
        </ValuesGrid>
      </ValuesSection>

      <ApproachSection>
        <ApproachContainer>
          <ApproachHeading
            initial={{ y: 50, opacity: 0 }}
            whileInView={{ y: 0, opacity: 1 }}
            transition={{ duration: 0.6 }}
            viewport={{ once: true }}
          >
            alma Yaklammz
          </ApproachHeading>
          
          <ContentText
            initial={{ y: 50, opacity: 0 }}
            whileInView={{ y: 0, opacity: 1 }}
            transition={{ duration: 0.6, delay: 0.2 }}
            viewport={{ once: true }}
          >
            Ard Psikoloji'de her dananmz btncl bir yaklamla deerlendiriyoruz. Bireysel ihtiyalar ve
            hedefleri gz nnde bulundurarak, en uygun terapi yntemlerini ve teknikleri bir araya getiriyoruz.
            Dananlarmzn aktif katlmn destekliyor ve onlarla ibirlii iinde alarak en etkili sonular
            elde etmeyi amalyoruz.
          </ContentText>

          <ContentText
            initial={{ y: 50, opacity: 0 }}
            whileInView={{ y: 0, opacity: 1 }}
            transition={{ duration: 0.6, delay: 0.4 }}
            viewport={{ once: true }}
          >
            Srekli eitim ve geliimi nemsiyor, psikoloji alanndaki gncel aratrmalar ve yeni yaklamlar 
            takip ederek hizmet kalitemizi srekli ykseltiyoruz. Her dananmzn kendini gvende ve anlalm 
            hissettii bir ortam yaratmak nceliimizdir.
          </ContentText>
        </ApproachContainer>
      </ApproachSection>
    </MissionContainer>
  );
};

export default Mission;