/**
 * Input ve form validasyonlar iin kullanlacak gvenlik fonksiyonlar
 */

/**
 * XSS saldrlar iin tehlikeli karakterleri temizler
 * @param {string} input - Kullanc girdisi
 * @returns {string} - Temizlenmi girdi
 */
export const sanitizeInput = (input) => {
  if (!input) return '';
  
  // HTML zel karakterlerini escape eder
  return input
    .replace(/&/g, '&amp;')
    .replace(/</g, '&lt;')
    .replace(/>/g, '&gt;')
    .replace(/"/g, '&quot;')
    .replace(/'/g, '&#039;');
};

/**
 * SQL enjeksiyon saldrlarna kar koruma
 * @param {string} input - Kullanc girdisi
 * @returns {boolean} - Girdi gvenli mi?
 */
export const isSqlInjectionSafe = (input) => {
  if (!input) return true;
  
  const sqlPattern = /(\b(select|insert|update|delete|drop|alter|create|where|from|having|join)\b)|(--|\/\*|\*\/|;)/i;
  return !sqlPattern.test(input);
};

/**
 * E-posta adreslerinin geerli formatn kontrol eder
 * @param {string} email - E-posta adresi
 * @returns {boolean} - E-posta geerli mi?
 */
export const isValidEmail = (email) => {
  if (!email) return false;
  const emailPattern = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
  return emailPattern.test(email);
};

/**
 * Telefon numaras formatn kontrol eder
 * @param {string} phone - Telefon numaras
 * @returns {boolean} - Telefon numaras geerli mi?
 */
export const isValidPhone = (phone) => {
  if (!phone) return false;
  // Trkiye telefon format (istee bal banda +90 veya 0 ile balayan 10 haneli numara)
  const phonePattern = /^(\+90|0)?[0-9]{10}$/;
  return phonePattern.test(phone.replace(/\s+/g, ''));
};

/**
 * Formlar gndermeden nce tm input deerlerini kontrol eden fonksiyon
 * @param {Object} formData - Form verileri
 * @returns {Object} - Hata mesajlar ve formun geerli olup olmad bilgisi
 */
export const validateForm = (formData) => {
  const errors = {};
  let isValid = true;
  
  // Ad Soyad kontrol
  if (!formData.name || formData.name.length < 3) {
    errors.name = 'Ltfen geerli bir ad ve soyad girin';
    isValid = false;
  }
  
  // SQL enjeksiyon kontrol
  if (!isSqlInjectionSafe(formData.name) || 
      !isSqlInjectionSafe(formData.email) || 
      !isSqlInjectionSafe(formData.phone) || 
      !isSqlInjectionSafe(formData.message)) {
    errors.general = 'Geersiz karakter kombinasyonu tespit edildi';
    isValid = false;
  }
  
  // E-posta kontrol
  if (!isValidEmail(formData.email)) {
    errors.email = 'Ltfen geerli bir e-posta adresi girin';
    isValid = false;
  }
  
  // Telefon kontrol
  if (!isValidPhone(formData.phone)) {
    errors.phone = 'Ltfen geerli bir telefon numaras girin';
    isValid = false;
  }
  
  // Mesaj kontrol
  if (!formData.message || formData.message.length < 10) {
    errors.message = 'Ltfen en az 10 karakter ieren bir mesaj yazn';
    isValid = false;
  }
  
  return { isValid, errors };
};